import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import I18n from './lang';

import { Switch, List, Search, Checkbox, Popup } from 'vant';

import 'vant/lib/switch/style/index'
import 'vant/lib/list/style/index'
import 'vant/lib/search/style/index'
import 'vant/lib/checkbox/style/index'
import 'vant/lib/popup/style/index'
import '@/styles/custom.scss'

createApp(App)
.use(Switch)
.use(List)
.use(Search)
.use(Checkbox)
.use(Popup)
.use(I18n)
.use(router)
.mount('#app')
